/* 3.2 couple-section */

.couple-section {
	.couple-area {
		background: #fbf8f8;
		height: 345px;
		display: flex;
		align-items: center;
		justify-content: center;

		@include media-query(1200px) {
			height: 280px;
		}
		@include media-query(991px) {
			flex-wrap: wrap;
			height: 100%;
			padding: 50px 0;
		}
	}

	.couple-area > div {
		float: left;

		@include media-query(991px) {
			float: none;
		}
	}

	.middle-couple-pic {
		width: 460px;
		position: relative;
		height: 460px;
		display: flex;
		justify-content: center;
		flex-direction: column;
		padding: 20px;
		box-shadow: 0px 0px 6.1px 3.9px rgba(28, 31, 86, 0.06);
		background: $white;
		border-radius: 235px;
		overflow: hidden;

		@include media-query(1199px) {
			width: 360px;
			height: 360px;
		}

		@include media-query(500px) {
			width: 290px;
			height: 290px;
		}

		.middle-couple-pic-inner {
			width: 420px;
			height: 420px;
			border-radius: 235px;
			overflow: hidden;
			@include media-query(1199px) {
				width: 320px;
				height: 320px;
			}
			@include media-query(500px) {
				width: 250px;
				height: 250px;
			}
			img {
				width: 100%;
				height: 100%;
				border-radius: 235px;
				-webkit-animation: kenburn 25s 2s alternate infinite;
				-animation: kenburn 25s 2s alternate infinite;
			}
		}

		@include media-query(991px) {
			margin: 50px auto;
		}
	}

	.text-grid {
		width: 29%;
		padding: 40px 80px 40px;

		@include media-query(1200px) {
			padding: 20px 30px 20px;
		}
		@include media-query(991px) {
			width: 100%;
			padding: 0;
			text-align: center;
		}

		h3 {
			font-size: 30px;
			font-size: calc-rem-value(30);
			margin: 0 0 0.6em;
			font-weight: 400;
		}
		p {
			margin-bottom: 10px;

			@include media-query(991px) {
				max-width: 570px;
				margin: 0 auto;
				margin-bottom: 10px;
				padding: 0 10px;
			}
		}
	}

	.bride {
		padding-left: 0;
		text-align: right;

		@include media-query(991px) {
			text-align: center;
			margin: 0 auto;
		}

		.social {
			float: right;

			@include media-query(991px) {
				float: none;
				display: flex;
				justify-content: center;
			}
		}
		.couple-img {
			margin: 0;
			margin-bottom: 20px;
			margin-left: auto;

			@include media-query(991px) {
				text-align: center;
				margin: 0 auto;
				margin-bottom: 20px;
			}
		}
	}

	.groom {
		padding-right: 0;
	}

	ul {
		list-style: none;
		overflow: hidden;
		padding-top: 15px;

		@include media-query(991px) {
			padding-top: 10px;
			display: flex;
			justify-content: center;
		}

		li {
			float: left;
		}

		> li + li {
			margin-left: 25px;
		}

		li a {
			display: block;
			text-align: center;
			color: $theme-primary-color;

			&:hover {
				color: $theme-primary-color;
			}
		}
	}
}

/* 3.3 wpo-story-section */

.wpo-story-section {
	padding-top: 50px;

	.tab-area {
		.nav-tabs {
			justify-content: center;
			border: 0;
			margin-bottom: 40px;

			.nav-item {
				.nav-link {
					border: 0;
					font-size: 22px;
					color: $text-color;
					padding: 10px 30px;
					cursor: pointer;

					&.active {
						color: $theme-primary-color;
					}

					@media (max-width: 991px) {
						font-size: 17px;
						padding: 10px 15px;
					}
				}
			}
		}

		.wpo-story-item {
			display: flex;
			align-items: center;
			box-shadow: 0px 0px 6.1px 3.9px rgba(28, 31, 86, 0.06);

			@media (max-width: 991px) {
				display: block;
			}

			.wpo-story-img {
				flex-basis: 50%;
				overflow: hidden;
				position: relative;

				&:before {
					position: absolute;
					left: 3%;
					top: 4%;
					width: 94%;
					height: 92%;
					content: "";
					border: 1px solid $white;
					z-index: 11;
				}

				img {
					-webkit-animation: kenburn 25s 2s alternate infinite;
					-animation: kenburn 25s 2s alternate infinite;

					@media (max-width: 1200px) {
						min-height: 415px;
						object-fit: cover;
					}
					@media (max-width: 991px) {
						min-height: 100%;
					}
				}
			}
			.wpo-story-content {
				flex-basis: 50%;
				padding: 45px 50px;

				@media (max-width: 1400px) {
					padding: 40px;
				}
				@media (max-width: 991px) {
					padding-top: 60px;
				}

				@media (max-width: 500px) {
					padding-left: 20px;
				}

				.wpo-story-content-inner {
					padding: 50px 60px 20px 0;
					border-top: 1px solid #d1c3c5;
					border-right: 1px solid #d1c3c5;
					position: relative;

					@media (max-width: 1400px) {
						padding: 20px 60px 0px 0;
					}
					@media (max-width: 500px) {
						padding: 30px 12px 0px 0;
					}

					.border-shape {
						position: absolute;
						right: -30px;
						top: -30px;
					}

					h2 {
						font-size: 35px;
						margin-top: 30px;
						margin-bottom: 10px;
						font-weight: 400;

						@media (max-width: 500px) {
							font-size: 24px;
						}
					}

					span {
						color: $theme-primary-color;
						font-size: 18px;
						display: block;
						margin-bottom: 20px;
					}

					p {
						color: $text-light-color;
					}
				}
			}
		}
	}
}

