/* 3.5 wpo-portfolio-section*/

.wpo-portfolio-section,
.wpo-portfolio-section-s3 {
	.portfolio-grids {
		margin: 0 -7.5px;
		.grid {
			width: 33.33%;
			float: left;
			padding: 0 7.5px 15px;

			@include media-query(1500px) {
				padding: 0 5.5px 10px;
			}
			@include media-query(991px) {
				padding: 0 4.5px 8px;
			}

			@include media-query(1199px) {
				width: 50%;
			}

			@include media-query(600px) {
				width: 100%;
			}

			img {
				width: 100%;
				max-height: 284px;
				object-fit: cover;
			}
		}
	}

	.grid {
		.img-holder {
			position: relative;
			.hover-content {
				transition: all 0.3s;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				opacity: 0;
				text-align: center;
				width: 260px;

				span {
					color: $text-color;
				}

				h4 {
					font-size: 30px;
					font-weight: 600;

					a {
						color: $dark-gray;

						&:hover {
							color: $theme-primary-color;
						}
					}
				}

				i {
					font-size: 30px;
					color: $dark-gray;
				}
			}

			&:before {
				position: absolute;
				left: 2%;
				top: 2%;
				width: 96%;
				height: 96%;
				content: "";
				background: rgba(255, 255, 255, 0.8);
				opacity: 0;
				transition: all 0.3s;
				transform: scale(0);
			}

			.react-fancybox {
				.thumbnail {
					&:before {
						position: absolute;
						left: 2%;
						top: 2%;
						width: 96%;
						height: 96%;
						content: "\e61a";
						background: rgba(255, 255, 255, 0.8);
						opacity: 0;
						transition: all 0.3s;
						transform: scale(0);
						font-family: "themify";
						display: flex;
						justify-content: center;
						flex-direction: column;
						text-align: center;
						font-size: 30px;
						color: #002642;
						cursor: pointer;
					}

					&:hover {
						&:before {
							opacity: 1;
							transform: scale(1);
						}
					}

					img {
						max-width: 100%;
						object-position: top;
					}
				}

				.box {
					z-index: 9999;
				}
			}
			.close-btn {
				width: unset;
				cursor: pointer;
			}
		}
	}

	.img-holder:hover .hover-content {
		opacity: 1;
	}
	.img-holder:hover {
		&:before {
			opacity: 1;
			transform: scale(1);
		}
	}
}

/* 3.6 wpo-contact-section */

.wpo-contact-section {
	padding-bottom: 200px;

	@media (max-width: 991px) {
		padding-bottom: 180px;
	}
	@media (max-width: 767px) {
		padding-bottom: 140px;
	}
	@media (max-width: 575px) {
		padding-bottom: 80px;
	}

	.wpo-contact-section-wrapper {
		position: relative;
		max-width: 650px;
		margin: 0 auto;
		padding: 20px;
		box-shadow: 0px 2px 16.8px 3.2px rgba(0, 0, 0, 0.08);
		background: $white;
		border-radius: 322px;

		@media (max-width: 1200px) {
			max-width: 570px;
		}

		.wpo-section-title {
			@media (max-width: 1200px) {
				margin-bottom: 20px;
			}
		}

		.vector-1 {
			position: absolute;
			left: -30%;
			bottom: -10%;
			z-index: -1;

			@media (max-width: 575px) {
				display: none;
			}
		}
		.vector-2 {
			position: absolute;
			right: -26%;
			top: -3%;
			z-index: -1;
			@media (max-width: 575px) {
				display: none;
			}
		}
		.wpo-contact-form-area {
			padding: 130px 60px;
			border: 1px solid $theme-primary-color;
			position: relative;
			z-index: 99;
			border-radius: 322px;

			@media (max-width: 1200px) {
				padding: 80px 20px;
			}
			@media (max-width: 575px) {
				padding: 50px 15px;
			}

			.form-control {
				width: 100%;
				height: 50px;
				margin-bottom: 20px;
				border: 0;
				border-bottom: 1px solid #cde0ef;
				background: transparent;
				border-radius: 0;
				color: #787878;
				padding-left: 0;
				font-size: 18px;

				&:focus {
					outline: none;
					box-shadow: none;
				}
			}

			.form-field p {
				color: red;
			}

			.form-control::-webkit-input-placeholder {
				/* Edge */
				color: #787878;
			}

			.form-control:-ms-input-placeholder {
				/* Internet Explorer 10-11 */
				color: #787878;
			}

			.form-control::placeholder {
				color: #787878;
			}

			select.form-control {
				color: #6c757d;
				-webkit-appearance: none;
				-ms-appearance: none;
				-o-appearance: none;
				appearance: none;
				-moz-appearance: none;
				background: transparent url(../../images/select-icon2.png) no-repeat calc(100% - 15px) center;
				position: relative;
			}

			.select.last {
				margin-bottom: 50px;
			}
			.submit-area {
				margin-top: 40px;
				text-align: center;
			}
		}
	}
}

/* 3.7 wpo-event-section */

.wpo-event-section {
	padding-bottom: 90px;
	position: relative;
	z-index: 1;

	@media (max-width: 767px) {
		padding-bottom: 70px;
	}

	&:before {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 500px;
		content: "";
		background: url(../../images/us/the-thief_15196666027_o_small.jpg) no-repeat center center;
		z-index: -1;
	}
	&:after {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 500px;
		content: "";
		background: rgba(94, 86, 86, 0.6);
		z-index: -1;
	}
	.wpo-section-title {
		h2 {
			color: $white;
		}
	}
	.wpo-event-wrap {
		.wpo-event-item {
			position: relative;
			margin-bottom: 40px;
			box-shadow: 0px 0px 6.1px 3.9px rgba(28, 31, 86, 0.06);
			background: $white;
			padding: 30px;

			@media (max-width: 1400px) {
				padding: 15px;
			}

			.wpo-event-text {
				padding: 40px 35px;
				text-align: center;
				padding-bottom: 15px;
				@media (max-width: 1400px) {
					padding: 40px 0px 20px;
				}

				h2 {
					font-size: 25px;
					text-transform: uppercase;
					margin-bottom: 25px;
					color: $theme-primary-color;
					font-weight: 400;
				}

				ul {
					list-style: none;

					li {
						margin-bottom: 15px;
						font-size: 18px;
						line-height: 30px;

						&:first-child {
							max-width: 225px;
							margin: 0 auto;
							margin-bottom: 15px;
						}

						&:last-child {
							margin-bottom: 0;
						}

						button {
							font-weight: 500;
							color: $theme-primary-color;
							position: relative;
							display: inline-block;
							margin-top: 10px;
							font-size: 16px;
							text-transform: capitalize;

							&:before {
								position: absolute;
								left: 0;
								bottom: 0;
								width: 100%;
								height: 1px;
								content: "";
								background: $theme-primary-color;
							}

							&:hover {
								background: none;
							}
							&:focus {
								background: none;
								outline: none;
								box-shadow: none;
							}
						}
					}
				}
			}
		}
	}
}
.MuiDialog-paperWidthSm {
	max-width: 900px !important;
}
.quickview-dialog {
	z-index: 999999 !important;
}
.modal-body {
	iframe {
		width: 800px;
		height: 500px;

		@media (max-width: 991px) {
			width: 500px;
		}
		@media (max-width: 600px) {
			width: 100%;
		}
	}
}

.MuiDialogTitle-root {
	padding-bottom: 0 !important;
}

/* 3.8 wpo-partners-section */

.wpo-partners-section {
	.container {
		position: relative;
	}

	.grid {
		text-align: center;
	}

	.grid img {
		width: auto;
		margin: 0 auto;
		display: inline-block;
	}

	.owl-nav {
		display: none;
	}
}

/* 3.9 wpo-blog-section */

.wpo-blog-section {
	padding-bottom: 90px;

	@media (max-width: 767px) {
		padding-bottom: 60px;
	}
	.wpo-blog-item {
		margin-bottom: 30px;

		.wpo-blog-img {
			overflow: hidden;
			img {
				width: 100%;
				-webkit-filter: grayscale(0);
				-moz-filter: grayscale(0);
				-o-filter: grayscale(0);
				-ms-filter: grayscale(0);
				filter: grayscale(0);
				transition: all 0.3s;
				transform: scale(1);
			}
		}

		&:hover {
			.wpo-blog-img {
				img {
					-webkit-filter: grayscale(100%);
					-moz-filter: grayscale(100%);
					-o-filter: grayscale(100%);
					-ms-filter: grayscale(100%);
					filter: grayscale(100%);
					transform: scale(1.2);
				}
			}
		}

		.wpo-blog-content {
			padding-top: 20px;
			ul {
				list-style: none;
				display: flex;
				margin-bottom: 15px;
				li {
					color: $theme-primary-color-s2;
					font-size: 16px;
					&:first-child {
						padding-right: 30px;
						position: relative;

						&:before {
							position: absolute;
							right: 10px;
							top: 50%;
							transform: translateY(-50%);
							width: 6px;
							height: 6px;
							content: "";
							background: $theme-primary-color-s2;
							border-radius: 50%;
						}
					}
					a {
						color: $theme-primary-color-s2;

						&:hover {
							color: $theme-primary-color;
						}
					}
				}
			}
			h2 {
				font-size: 30px;
				font-family: $heading-font;
				margin-bottom: 20px;
				line-height: 36px;
				font-weight: 400;
				margin-top: 10px;

				@media (max-width: 1200px) {
					font-size: 25px;
					margin-bottom: 10px;
				}

				a {
					color: $heading-color;

					&:hover {
						color: $theme-primary-color;
					}
				}
			}

			p {
				font-size: 18px;
				color: $text-color;
			}
		}
	}
}

.wedding-images {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 22px;
}

.wedding-images img {
	max-height: 900px;
	margin: auto;
}

@media (max-width: 550px) {
	.wedding-images {
		grid-template-columns: 1fr;
	}
}